/* Variables */
:root {
    /* Color System - Lebanese-inspired palette */
    --lebify-red-50: #ffeaed;
    --lebify-red-100: #ffd5db;
    --lebify-red-200: #ffabb7;
    --lebify-red-300: #ff8293;
    --lebify-red-400: #ff586f;
    --lebify-red-500: #ff2e4b;
    --lebify-red-600: #ce1126;
    /* Lebanese flag red */
    --lebify-red-700: #a60d1e;
    --lebify-red-800: #7e0916;
    --lebify-red-900: #56050e;

    --lebify-green-50: #e6f7ef;
    --lebify-green-100: #ccf0df;
    --lebify-green-200: #99e1bf;
    --lebify-green-300: #66d29f;
    --lebify-green-400: #33c37f;
    --lebify-green-500: #00b45f;
    --lebify-green-600: #00a651;
    /* Lebanese cedar green */
    --lebify-green-700: #008541;
    --lebify-green-800: #006431;
    --lebify-green-900: #004320;

    --lebify-white: #fffcf7;
    --lebify-gray-50: #f9f9f9;
    --lebify-gray-100: #f3f3f3;
    --lebify-gray-200: #e6e6e6;
    --lebify-gray-300: #d9d9d9;
    --lebify-gray-400: #cccccc;
    --lebify-gray-500: #999999;
    --lebify-gray-600: #666666;
    --lebify-gray-700: #4d4d4d;
    --lebify-gray-800: #333333;
    --lebify-gray-900: #1a1a1a;
    --lebify-black: #000000;

    /* Semantic Colors */
    --lebify-primary: var(--lebify-red-600);
    --lebify-secondary: var(--lebify-green-600);
    --lebify-tertiary: var(--lebify-white);
    --lebify-tertiary-border: var(--lebify-red-600);

    --lebify-text-on-primary: var(--lebify-white);
    --lebify-text-on-secondary: var(--lebify-white);
    --lebify-text-on-tertiary: var(--lebify-gray-800);

    --lebify-text-primary: var(--lebify-gray-800);
    --lebify-text-secondary: var(--lebify-gray-600);
    --lebify-text-disabled: var(--lebify-gray-400);

    /* App Background Colors */
    --lebify-app-background: var(--lebify-white);
    --lebify-app-surface: var(--lebify-gray-50);
    --lebify-app-border: var(--lebify-gray-200);

    /* Typography */
    --lebify-font-family-heading: 'Changa', sans-serif;
    --lebify-font-family-body: 'Nunito', sans-serif;
    --lebify-font-family: var(--lebify-font-family-body);

    --lebify-font-size-xs: 12px;
    --lebify-font-size-sm: 14px;
    --lebify-font-size-md: 16px;
    --lebify-font-size-lg: 18px;
    --lebify-font-size-xl: 20px;
    --lebify-font-size-2xl: 24px;
    --lebify-font-size-3xl: 30px;
    --lebify-font-size-4xl: 36px;

    --lebify-font-weight-light: 300;
    --lebify-font-weight-regular: 400;
    --lebify-font-weight-medium: 500;
    --lebify-font-weight-semibold: 600;
    --lebify-font-weight-bold: 700;

    --lebify-line-height-tight: 1.2;
    --lebify-line-height-normal: 1.5;
    --lebify-line-height-loose: 1.8;

    /* Spacing */
    --lebify-spacing-xs: 4px;
    --lebify-spacing-sm: 8px;
    --lebify-spacing-md: 16px;
    --lebify-spacing-lg: 24px;
    --lebify-spacing-xl: 32px;
    --lebify-spacing-2xl: 48px;
    --lebify-spacing-3xl: 64px;

    /* Borders */
    --lebify-border-radius-sm: 4px;
    --lebify-border-radius-md: 8px;
    --lebify-border-radius-lg: 12px;
    --lebify-border-radius-pill: 9999px;

    --lebify-border-width-thin: 1px;
    --lebify-border-width-normal: 2px;
    --lebify-border-width-thick: 3px;

    /* Shadows */
    --lebify-shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
    --lebify-shadow-md: 0 4px 6px rgba(0, 0, 0, 0.1);
    --lebify-shadow-lg: 0 10px 15px rgba(0, 0, 0, 0.1);

    /* Transitions */
    --lebify-transition-fast: all 0.2s ease;
    --lebify-transition-normal: all 0.3s ease;
    --lebify-transition-slow: all 0.5s ease;

    /* Z-index system */
    --lebify-z-index-dropdown: 1000;
    --lebify-z-index-sticky: 1100;
    --lebify-z-index-fixed: 1200;
    --lebify-z-index-modal-backdrop: 1300;
    --lebify-z-index-modal: 1400;
    --lebify-z-index-popover: 1500;
    --lebify-z-index-tooltip: 1600;
}

/* Dark mode theme */
[data-theme="dark"] {
    --lebify-primary: var(--lebify-red-500);
    --lebify-secondary: var(--lebify-green-500);
    --lebify-tertiary: var(--lebify-gray-800);
    --lebify-tertiary-border: var(--lebify-red-500);

    --lebify-text-primary: var(--lebify-gray-100);
    --lebify-text-secondary: var(--lebify-gray-300);
    --lebify-text-disabled: var(--lebify-gray-600);

    /* App Background Colors for Dark Mode */
    --lebify-app-background: var(--lebify-gray-900);
    --lebify-app-surface: var(--lebify-gray-800);
    --lebify-app-border: var(--lebify-gray-700);
}

