/* Utility classes */
.lebify-d-block {
    display: block;
}

.lebify-d-inline {
    display: inline;
}

.lebify-d-inline-block {
    display: inline-block;
}

.lebify-d-flex {
    display: flex;
}

.lebify-d-inline-flex {
    display: inline-flex;
}

.lebify-d-grid {
    display: grid;
}

.lebify-d-none {
    display: none;
}

.lebify-flex-row {
    flex-direction: row;
}

.lebify-flex-column {
    flex-direction: column;
}

.lebify-justify-start {
    justify-content: flex-start;
}

.lebify-justify-end {
    justify-content: flex-end;
}

.lebify-justify-center {
    justify-content: center;
}

.lebify-justify-between {
    justify-content: space-between;
}

.lebify-justify-around {
    justify-content: space-around;
}

.lebify-align-start {
    align-items: flex-start;
}

.lebify-align-end {
    align-items: flex-end;
}

.lebify-align-center {
    align-items: center;
}

.lebify-align-stretch {
    align-items: stretch;
}

.lebify-m-0 {
    margin: 0;
}

.lebify-mt-0 {
    margin-top: 0;
}

.lebify-mr-0 {
    margin-right: 0;
}

.lebify-mb-0 {
    margin-bottom: 0;
}

.lebify-ml-0 {
    margin-left: 0;
}

.lebify-m-xs {
    margin: var(--lebify-spacing-xs);
}

.lebify-mt-xs {
    margin-top: var(--lebify-spacing-xs);
}

.lebify-mr-xs {
    margin-right: var(--lebify-spacing-xs);
}

.lebify-mb-xs {
    margin-bottom: var(--lebify-spacing-xs);
}

.lebify-ml-xs {
    margin-left: var(--lebify-spacing-xs);
}

.lebify-text-left {
    text-align: left;
}

.lebify-text-center {
    text-align: center;
}

.lebify-text-right {
    text-align: right;
}

.lebify-text-primary {
    color: var(--lebify-text-primary);
}

.lebify-text-secondary {
    color: var(--lebify-text-secondary);
}

.lebify-text-disabled {
    color: var(--lebify-text-disabled);
}

/* Card/container styles */
.lebify-card {
    background-color: var(--lebify-app-surface);
    border-radius: var(--lebify-border-radius-md);
    padding: var(--lebify-spacing-md);
    box-shadow: var(--lebify-shadow-sm);
    border: var(--lebify-border-width-thin) solid var(--lebify-app-border);
    transition: var(--lebify-transition-normal);
}